import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./AboutPageContent.module.scss"

export default () => {
  const { pine } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.headingContainer}>
        <div className={styles.headingContent}>
          <Img fixed={pine.childImageSharp.fixed} />
          <div className={styles.heading}>About Me</div>
          <Img
            fixed={pine.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
            imgStyle={{
              WebkitTransform: "scaleX(-1)",
              transform: "scaleX(-1)",
            }}
          />
        </div>
      </div>
    </React.Fragment>
  )
}
